import Message from './message.vue';

const Mes = {};
Mes.install = function(Vue, options){
	Vue.prototype.$message = (tips = 'tips', duration = 1500, fn) =>{
		let mesHTML =  Vue.extend(Message);
		let message = new mesHTML({
			tips,
			duration,
			fn
		}).$mount().$el;
		document.body.append(message);
	};
};
export default Mes;